.boss{
    margin-bottom: 132px;
}
.boss__wrapper {
    margin-left: -11px;
    display: flex;
    justify-content: space-between;
   
}
.boss__left {
    display: flex;
    align-items: flex-start;
}
.boss__info {
    margin-top: 53px;
    margin-left: 22px;
}

.boss__name {
    font-family: 'Raleway';
font-style: normal;
font-weight: 800;
font-size: 32px;
line-height: 38px;
letter-spacing: 0.01em;

color: #1A2F3F;
}
.boss__subtitle {
    font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 21px;
/* or 131% */
letter-spacing: 0.01em;
color: #1A2F3F;
width: 297px;
margin-left: 3px;
margin-top: 10px;

}
.main__meta {
    font-family: 'Raleway';
font-style: italic;
font-weight: 500;
font-size: 15px;
line-height: 20px;
position: relative;
letter-spacing: -0.08px;
width: 352px;
margin-left: 41px;
    margin-top: 38px;

color: #1A2F3F;
}
.main__meta::before{
    content: '';
    display: inline-block;
    width: 26px;
    height: 26px;
    background: url('../../img/svg/quotes.svg');
    position: absolute;
    left: -38px;
    top: -9px;
}
.boss__button {
    width: 188px;
height: 52px;
border: 1.5px solid #337D5A;
font-family: 'Raleway';
font-style: normal;
font-weight: 700;
font-size: 14px;
line-height: 16px;
letter-spacing: 0.02em;

color: #337D5A;
margin-top: 57px;
    margin-left: 1px;
}
.boss__right {
    .boss__item{
        background: url("../../img/bass-bg.png");
        height: 493px;
        width: 404px;
        margin-top: 20px;
        margin-right: 10px;
        padding-top: 31px;
        padding-left: 47px;
        background-repeat: no-repeat;
    }

}   
 .boss__title{
    font-family: "Raleway";
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    line-height: 38px;
    letter-spacing: 0.2px;
    color: #FFFFFF;
    margin-left: -2px;
    margin-bottom: 32px;
    }
    .boss__item-subtitle{
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        color: #FCFCFC;
        letter-spacing: -0.013em;
        width: 384px;
        margin-top: 15px;
        span{
            letter-spacing: 0.02px;
        }
        }